<script>
  import { socket } from '../stores/AppStore'
  import { currentUser } from '../stores/UserStore'
  import { rgbToHex, hexToRgb, formatDate } from '../functions/formats'
  import { page } from '../stores/AppStore'

  const updateProfile = (e) => {
    e.preventDefault()

    //format data
    let username = e.target.username.value.trim()
    let email = e.target.email.value.trim()
    let password = e.target.password.value.trim()
    let color = hexToRgb(e.target.color.value)
    let text = hexToRgb(e.target.text.value)

    //check different
    if (username === $currentUser.name) username = null
    if (email === $currentUser.email) email = null
    if (color === $currentUser.color) color = null
    if (text === $currentUser.text) text = null

    //Only send values that are different and need to be updated
    const payload = {
      _id: $currentUser._id,
      update: {
        ...(username && { name: username }),
        ...(email && { email }),
        ...(password && { password }),
        ...(color && { color }),
        ...(text && { text }),
      },
    }

    //nothing different return
    if (Object.keys(payload).length <= 1) return

    $socket.emit('user:update', payload, (response) => {
      $currentUser = response
      localStorage.setItem('currentUser', JSON.stringify(response))
      console.log(
        '%c  User Updated',
        'color:rgb(63, 153, 82); font-weight: bold;'
      )
      // console.dir(response)
    })
  }

</script>

{#if $currentUser.createdAt}
  <section class="profile ">
    <div class="page-nav">
      <input
        type="button"
        value=" Home"
        on:click={() => ($page = 'home')}
        on:keydown={null}
      />

      <input
        type="button"
        value="Chat "
        class="right"
        on:click={() => ($page = 'chat')}
        on:keydown={null}
      />
    </div>

    <form on:submit={updateProfile}>
      <div class="profile-card">
        <div class="card-top">
          <h2 class="left">USER</h2>
          <div class="middle">
            <div class="punch-out" />
          </div>
          <div class="right">
            <p class="issued">
              ISSUED: {formatDate($currentUser.createdAt)}
            </p>

            <p class="id">{$currentUser._id}</p>
          </div>
        </div>

        <div class="profile-pic-section">
          <svg
            class="background"
            viewBox="0 0 100 100"
            preserveAspectRatio="none"
          >
            <path d="M0,0 L100,0 L100,20 L0,90z" />
          </svg>
          <div class="profile-pic-container">
            <div class="pic-border">
              <div class="circle-mask">
                <img
                  class="profile-pic"
                  src="../assets/images/LittleGuy.svg"
                  alt="Little Guy SVG"
                />
              </div>
            </div>
          </div>
        </div>

        <div class="input-container">
          <div class="name-pass-container">
            <div class="input-group">
              <label for="username">NAME:</label>
              <input
                class="input"
                name="username"
                type="text"
                value={$currentUser.name}
              />
            </div>

            <div class="input-group">
              <label for="password">PASSWORD:</label>
              <input
                class="input"
                id="newPassword"
                name="password"
                type="password"
                placeholder="password..."
              />
            </div>
          </div>

          <label for="email">EMAIL:</label>
          <input
            class="input"
            name="email"
            type="email"
            value={$currentUser.email}
          />

          <label for="color">COLORS:</label>
          <div class="color-container">
            <input
              type="color"
              name="color"
              value={rgbToHex($currentUser.color)}
            />
            <input
              type="color"
              name="text"
              value={rgbToHex($currentUser.text)}
            />
          </div>
        </div>

        <div class="card-bottom">
          <h2>BenGomez.ID</h2>
        </div>
      </div>

      <label class="update-label" for="password">PASSWORD:</label>
      <div class="update-container">
        <input
          class="input"
          id="authPassword"
          name="password"
          type="password"
          placeholder="password..."
        />
        <button>UPDATE</button>
      </div>
    </form>

    <div class="delete-container">
      <button class="deleteUser">Delete User</button>
    </div>
  </section>
{:else}
  <section>
    <h1>User Not Found</h1>
    <input
      type="button"
      value=" Home"
      on:click={() => ($page = 'home')}
      on:keydown={null}
    />
  </section>
{/if}

<style>
  .profile {
    color: white;
    width: fit-content;
    margin: auto;
  }

  .page-nav {
    color: rgb(190, 190, 190);
    font-weight: 100;
    margin-bottom: 1rem;
  }

  .page-nav input {
    background-color: transparent;
    color: rgb(194, 194, 194);
  }

  .page-nav input:hover {
    cursor: pointer;
    color: rgb(255, 255, 255);
  }

  .page-nav .right {
    float: right;
  }

  .profile-card {
    display: block;
    background: linear-gradient(180deg, #22211e 0%, #0e0d0c 100%);
    box-shadow: 0px 10px 10px rgba(0, 0, 0, 0.82);
    margin-bottom: 1rem;
    border: rgba(255, 255, 255, 0.25) 1px solid;
    border-radius: 10px;
    font-family: 'Inter', sans-serif;
    max-width: 450px;
  }

  .card-top {
    background-color: var(--blue);
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    border-radius: 10px 10px 0 0;
    padding: 0.5rem;
    text-transform: uppercase;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  }

  .card-top .left {
    font-size: 1.5rem;
    font-weight: 900;
    height: 1.5rem;
  }

  .card-top .middle {
    margin: auto;
  }

  .card-top .punch-out {
    background-color: var(--black);
    width: 50px;
    height: 10px;
    border-radius: 10%/50%;
    box-shadow: inset 0px 4px 4px rgba(0, 0, 0, 0.25);
  }

  .card-top .right {
    flex-basis: 100%;
    text-align: right;
    height: 1.25rem;
  }

  .card-top .issued {
    font-size: 0.75rem;
    font-weight: 500;
  }

  .card-top .id {
    color: rgba(217, 213, 200, 0.75);
    font-family: monospace;
    font-size: 0.5rem;
    font-weight: 400;
  }

  .profile-pic-section {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: 1fr;
  }

  .background {
    fill: var(--white);
    grid-column: 1/4;
    grid-row: 1/2;

    width: 100%;
    height: 15rem;
  }

  .profile-pic-container {
    grid-column: 1/ 4;
    grid-row: 1/2;

    display: flex;
    align-items: center;
    justify-content: center;

    margin: 1rem;
  }

  .pic-border {
    background-color: rgb(113, 102, 87);
    border: solid 1px rgba(255, 255, 255, 0.25);
    border-radius: 50%;
    padding: 1rem;
    box-shadow: 0 5px 5px rgba(0, 0, 0, 0.25);
  }

  .circle-mask {
    background: linear-gradient(
      -45deg,
      rgb(24, 23, 21) 0%,
      rgb(75, 70, 58) 100%
    );

    display: flex;
    align-items: center;
    justify-content: center;

    width: 14rem;
    height: 14rem;
    border-radius: 50%;
    clip-path: circle(50% at 50% 50%);
  }

  .profile-pic {
    width: 135px;
    transform: translate(8px, 30px) rotate(-15deg);
  }

  .circle-mask:hover .profile-pic {
    transform: translate(8px, 30px) rotate(-18deg);
  }

  .input-container {
    padding: 2rem;
  }

  .name-pass-container {
    display: flex;
    gap: 0.5rem;
  }

  label {
    display: block;
    color: #d9d5c8;
    font-weight: bold;
    font-size: 1rem;
    margin-bottom: 5px;
  }

  .input {
    background-color: #32312d;
    color: white;
    display: block;
    width: 100%;
    margin-bottom: 1rem;
    border-radius: 5px;
    padding: 0.5rem;
    font-size: 1rem;
  }

  .color-container {
    display: flex;
    width: 100%;
    margin-bottom: 1rem;
    border-radius: 5px;
    padding: 0;
    gap: 0.5rem;
  }

  input[type='color'] {
    width: 100%;
    height: 2rem;
  }

  input[type='color']::-webkit-color-swatch-wrapper {
    padding: 0;
  }

  input[type='color']:hover {
    border: 1px solid rgba(255, 255, 255);
    border-radius: 3px;
  }

  input[type='color']:focus {
    border: 1px solid rgba(255, 255, 255);
    border-radius: 3px;
  }

  .card-bottom {
    background-color: var(--white);
    border-radius: 0 0 10px 10px;
    border-bottom: solid 10px var(--orange);
  }

  .card-bottom h2 {
    font-family: 'Pacifico', cursive;
    font-weight: 400;
    font-size: 1.25rem;
    color: var(--blue);
    padding: 0.5rem;
  }

  .update-container {
    display: flex;
    gap: 0.5rem;
  }

  .update-label {
    color: rgba(240, 255, 255, 0.514);
  }

  .update-container input {
    margin: 0;
  }

  .update-container button {
    width: fit-content;
    float: right;
    padding: 0 1rem;
  }

  .delete-container {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    padding: 3rem;
  }

  .delete-container button {
    background-color: transparent;
    color: rgb(255, 70, 70);
    width: fit-content;
    padding: 5px;
  }
</style>
